export const loader = () => {
    localStorage.setItem('isFirsttime', "true")
    localStorage.setItem('EventStatusData', JSON.stringify(initstatusdata));
    localStorage.setItem('TaskData', JSON.stringify(TaskData));
    localStorage.setItem('Activitiesdata', JSON.stringify(Activitiesdata));
    localStorage.setItem('Quizsdata', JSON.stringify(Quizsdata));
    localStorage.setItem('RewardTextData', JSON.stringify(RewardTextData));
    var formData = new FormData()
    formData.append('username', "guest");
    formData.append('lastname', "guestF");
    formData.append('firstname', "guestL");
    formData.append('auth_provider', "guest");
    Sociallogin((data: any, errorresponse: any) => {
        if (data.status === 200) {
            localStorage.setItem('AuthToken', JSON.stringify(data.data.result.token));
            localStorage.setItem('UserDetails', JSON.stringify(data.data.result.user_details));
        } else {
            console.log('error ' + JSON.stringify(data));
            console.log('error ' + JSON.stringify(errorresponse));
        }
    }, formData)
}
