import React, { useEffect, useState } from 'react'
import './style.css'
import { useVoiceRecorder } from "use-voice-recorder";
import { ReactMic } from 'react-mic';

const AudioRecord = () => {

    const [records, updateRecords] = useState([]);
    const { isRecording, stop, start } = useVoiceRecorder((data) => {
        updateRecords([...records, window.URL.createObjectURL(data)]);
    });

    const [isrecord, setrecord] = useState(true);
    const [isrecordstop, setrecordstop] = useState(false);


    const startRecording = (e) => {
        setrecord(e)
    }

    const stopRecording = (e) => {
        setrecordstop(e)
    }


    return (
        <>
            <div className={'container'}>
                <div className={'hint'}>
                    Just hold the mic button and speak.
                </div>
                <div onClick={stopRecording}>
                    <button className={`btn ${isRecording ? 'active' : ''}`}
                        onMouseDown={start}
                        onMouseUp={stop}
                        onTouchStart={start}
                        onTouchEnd={stop}>🎙</button>

                    <h4 className={'onair'}>On air: {isRecording ? 'on' : 'off'}</h4>
                </div>
                <div>
                    <ReactMic
                        record={startRecording}
                        className="sound_wave"
                        onStop={stopRecording}
                        strokeColor="#000000"
                        backgroundColor="#FF4081" />
                    {/* <button  type="button">Start</button>
                <button onClick={stopRecording} type="button">Stop</button> */}
                </div>
                <div className={'records'}>
                    <h3>Records:</h3>
                    {records.map((data, idx) => (
                        <div key={idx}>
                            <audio src={data} controls preload={'metadata'} />
                        </div>
                    ))}
                </div>

            </div>


        </>
    )
}

export default AudioRecord
